import React, { useState } from 'react'

const AddCommentForm = ({ articleName, setArticleInfo }) => {
  const addComment = async (username, commentText) => {
    const result = await fetch(`/api/articles/${articleName}/add-comment`, {
      method: 'post',
      body: JSON.stringify({
        username,
        text: commentText
      }), 
      headers: {
        'Content-Type': 'application/json'
      }
    })
    
    const body = await result.json()
    setArticleInfo(body)
    setUsername('')
    setCommentText('')
  }
  const [username, setUsername] = useState('')
  const [commentText, setCommentText] = useState('')
  return (<div id ='add-comment-form'>
    <h3>Add</h3>
    <label>Name:
      <input type = 'text' value ={username} onChange={(e) => setUsername(e.target.value)}/>
    </label>
    <label>Comment:
      <textarea rows='4'cols='50' value ={commentText} onChange={(e) => setCommentText(e.target.value)}/>
    </label>
    <button type='submit' onClick= {() => addComment(username, commentText) }>Add Comment</button>
  </div>)
}

export default AddCommentForm